/* css reset */
* {
    box-sizing: border-box;
}

*::before,
*::after {
    box-sizing: border-box;
}

* {
    margin: 0;
    padding: 0;
}

ul,
ol {
    list-style: none;
}

/* style */
body {
    background: #eee;
}

.globalHeader {
    margin: 40px 20px;
}

@media(min-width:500px) {
    .globalHeader {
        margin: 100px;
    }
}

.searchForm {
    display: flex;
    justify-content: space-between;
}

@media(min-width:500px) {
    .searchForm {
        max-width: 400px;
        margin-left: auto;
        margin-right: auto;
    }
}


.searchForm>input {
    width: 100%;
    margin-right: 10px;
    height: 40px;
    padding: 0 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.searchForm>button {
    white-space: nowrap;
    padding: 0 28px;
    border-radius: 4px;
    background: #3781C6;
    border: none;
    color: white;
    font-size: 16px;
}

.globalMain {
    margin-left: auto;
    margin-right: auto;
}

.siteList {
    margin: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

}

@media(min-width:500px) {
    .siteList {
        width: 800px;
        margin-right: auto;
        margin-left: auto;
        justify-content: flex-start;
    }
}

.siteList>li {
    margin-bottom: 10px;
}

@media(min-width:500px) {
    .siteList>li {
        width: 25%;
    }
}

.siteList .site {
    width: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: white;
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 20px 0;
    position: relative;
    cursor: pointer;
}

.siteList .site>.logo {
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 64px;
}

.siteList .site>.link {
    font-size: 14px;
    margin-top: 4px;
}

.siteList .site>.close {
    position: absolute;
    right: 10px;
    top: 5px;
    cursor: default;
}

@media(min-width:500px) {
    .siteList .site>.close {
        display: none;
    }
}

.siteList .site:hover>.close {
    display: block;
}

.siteList .addButton {
    width: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px 0;
    background: white;
    border: 1px solid #ddd;
}

.siteList .addButton .icon {
    width: 56px;
    height: 56px;
}

.siteList .addButton .text {
    font-size: 14px;
    margin-top: 4px;
}

.siteList .addButton .iconWrapper {
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
}